.house {

  &__hat {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__links {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    & a {
      & svg {
        width: 17px;
        height: 17px;
        margin-left: 20px;
      }
    }
  }

  &__title {
    text-align: center;
  }
}
